const styles = ({ palette, spacing: { unit } }) => ({
  placeholderIcon: {
    color: palette.secondary.light,
    marginTop: 5 * unit,
    marginBottom: 5 * unit,
    marginLeft: 5 * unit,
    marginRight: 10 * unit,
    height: 21 * unit,
    width: 21 * unit
  }
});

export default styles;



